'use strict';

/**
* Compute a moving arithmetic mean of absolute values incrementally.
*
* @module @stdlib/stats/incr/mmeanabs
*
* @example
* var incrmmeanabs = require( '@stdlib/stats/incr/mmeanabs' );
*
* var accumulator = incrmmeanabs( 3 );
*
* var mu = accumulator();
* // returns null
*
* mu = accumulator( 2.0 );
* // returns 2.0
*
* mu = accumulator( -5.0 );
* // returns 3.5
*
* mu = accumulator( 3.0 );
* // returns ~3.33
*
* mu = accumulator( 5.0 );
* // returns ~4.33
*
* mu = accumulator();
* // returns ~4.33
*/

// MODULES //
var incrmmeanabs = __var_0__;


// EXPORTS //
export default incrmmeanabs;
